<template>
  <div id="app">
    <div class="con">
      <HelloWorld :hengTest="hengTest" v-if="check"/>
      <div class="inp">
        <span>输入球的个数(数量不要太大)：</span><input type="text" v-model="num1"><button @click="qrBtn()">确认</button>
      </div>
    </div>
  </div>
</template>

<script>
import HelloWorld from './components/HelloWorld.vue'

export default {
  name: 'App',
  components: {
    HelloWorld
  },
  data() {
    return {
      check:true,
      num1:9,
      num: 9,
      // hengTest:
      //       [
      //           {title: '行星1',num: 28.1,unit: '克',percent: '-30%'},
      //           {title: '行星2',num: 58.1,unit: '克',percent: '10%'},
      //           {title: '行星3',num: 25.1,unit: '克',percent: '+20%'},
      //           {title: '行星4',num: 72.1,unit: '克',percent: '+26%'},
      //           {title: '行星5',num: 98.11,unit: '克',percent: '-12%'},
      //           {title: '行星6',num: 671.1,unit: '克',percent: '+10%'},
      //           {title: '行星7',num: 671.1,unit: '克',percent: '+10%'},
      //           {title: '行星8',num: 671.1,unit: '克',percent: '+10%'},
      //           {title: '行星9',num: 671.1,unit: '克',percent: '+10%'},
      //       ]
    }
  },
  computed: {
    hengTest() {
      let a = []
      for (let i = 1; i <= this.num; i ++) {
        let b = {}
        b.title = '行星' + i
        b.num = i * 100
        b.unit = '克'
        b.percent = (Math.floor(Math.random() * 10) % 2 == 0 ? '+' : '-') + i + '%'
        // b.percent = Math.floor(Math.random() * 10) % 2 
        a.push(b)
      }
      return a
    }
  },
  methods: {
    qrBtn() {
      this.num = this.num1
      this.check = false
      this.check = true
    }
  }

}
</script>

