<template>
    <div class='content'>
        <div class='sphere'>
            <ul>
                <li v-for='(item,index) in hengTest' :key='index'>
                    <div class='star' :class="'star' + index" @click='clickEvent(index)'>
                        <div class='con'>
                            <!-- <slot :item='item'></slot> -->
                            <div class='con1'>{{item.title}}</div>
                            <div class='value'>
                                <strong>{{item.num}}</strong>
                                <span>{{item.unit}}</span>
                            </div>
                            <div class='ratio'>
                                相比：<span class='red'>{{item.percent}}</span>
                            </div>
                        </div>
                    </div>
                </li>
            </ul>
        </div>
        <div class='earth'></div>
        <div class='region-name'><span class='all' @click='allClick'>地球</span></div>
    </div>
</template>

<script>
//    style='animaition: move 10s infinite;'
export default {
    mounted(){
        this.updateEath()
        // 获取父容器的宽高
        var width = document.querySelector(".sphere").clientWidth
        var height = document.querySelector(".sphere").clientHeight
        this.ballRadius()
        this.ani_track(width, height, 0)
        this.ballInitPos()
        this.letBallSport(this.everyStepTime, 0)
        this.realTimeSport()
        this.updateRatioColor()
        this.updateByBall()
    },
    updated(){
         console.log('updated')
                 this.updateEath()
        // 获取父容器的宽高
        var width = document.querySelector(".sphere").clientWidth
        var height = document.querySelector(".sphere").clientHeight
        this.ballRadius()
        this.ani_track(width, height, 0)
        this.ballInitPos()
        this.letBallSport(this.everyStepTime, 0)
        this.realTimeSport()
        this.updateRatioColor()
        this.updateByBall()
    },
    beforeUpdate() {
        console.log('beforeUpdate')
    },
    beforeDestroy() {
        console.log('beforeDestroy')
    },
    destroyed() {
        clearInterval(this.parseTime1)
        clearInterval(this.parseTime2)
        clearTimeout(this.parseTime3)
        this.parseTime1 = null
        this.parseTime2 = null
        this.parseTime3 = null
        console.log('destroyed')
    },
    watch:{},
    props: {
        //传递的对象内容
        hengTest: {
            type: Array,
            default: () => {
                return [
                    {title: '小球0',num: 78.1,unit: '个',percent: '+10%'},
                    {title: '小球1',num: 28.1,unit: '万元',percent: '-30%'},
                    {title: '小球2',num: 58.1,unit: '万元',percent: '10%'},
                    {title: '小球3',num: 25.1,unit: '斤',percent: '+20%'},
                    {title: '小球4',num: 72.1,unit: '个',percent: '+26%'},
                    {title: '小球5',num: 98.11,unit: '元',percent: '-12%'},
                    {title: '小球6',num: 671.1,unit: '两',percent: '+10%'},
                    {title: '小球7',num: 671.1,unit: '两',percent: '+10%'},
                    {title: '小球8',num: 671.1,unit: '两',percent: '+10%'},
                    {title: '小球9',num: 671.1,unit: '两',percent: '+10%'},
                ]}
        },
        //设置一圈所需要的时间
        time: {
            type: Number,
            default: () => {
                return 20
            }
        },
        //设置小球被点击后，一圈所需要的时间
        clickTime: {
            type: Number,
            default: () => {
                return 5
            }
        },
        //设置小球停留的时间
        ballParseTime: {
            type: Number,
            default: () => {
                return 3
            }
        },
        //最近点放大倍数 
        maxScale: {
            // type: fl,
            default: () => {
                return 1.3
            }
        }, 
        //最远点缩小倍数
        minScale: {
            // type: fl,
            default: () => {
                return 0.7
            }
        },
        //运动帧数
        comSport: {
            type: Number,
            default: () => {
                return 700
            }
        },
        //路径的宽度比例
        widthRatio: {
            // type: Number,
            default: () => {
                return 0.41
            }
        },
        //路径的高度比例
        heightRatio: {
            // type: Number,
            default: () => {
                return 0.16
            }
        },
        //小球的半径
        ballR: {
            // type: Number,
            default: () => {
                return 0
            }
        },
    },
    data() {
    return {
        //保存小球运动轨迹的所有坐标,顺时针的坐标集
        ballSportXY: [],
        //保存小球运动轨迹逆时针的坐标集
        ballSportXYReverse: [],
        //动画帧 
        // com1: 660,
        //设置一圈所需要的时间
        // time: 20,
        //设置小球被点击后，一圈所需要的时间
        // clickTime: 5,
        //设置小球停留的时间
        // ballParseTime: 3,
        //定时器
        parseTime1: null,

        //letBallSport函数的变量
        //运动函数记录的变量
        step: 0,
        //随时记录每个小球的坐标点
        anyTimeXY: [],

        //realTimeSpor函数的变量
        //球的标记
        ballFlag:'',
        //定时器
        //setInterval
        parseTime2: null,
        //timeout
        parseTime3: null,
    }
  },
  computed: {
      //小球个数
      ballNUm() {
          return this.hengTest.length
      },
      //一个小球到下一个小球做过的帧数
      ballEveryStep() {
          return this.com / this.ballNUm
      },
      //每一帧需要的时间,正常运动
      everyStepTime() {
          return this.time / this.com * 1000
      },
      //小球被点击后，运动帧数
      clickTimeMS() {
          return this.clickTime / this.com * 1000
      },
      //小球停留时间
      ballParseTimeMS() {
          return this.ballParseTime * 1000
      },
      //运动帧数
      com() {
          return parseInt(this.comSport / this.ballNUm) * this.ballNUm
      },
      //小球得半径
      ballRR() {
          if (this.ballR == 0) {
                let ball = document.querySelector(".star")
                let r = window.getComputedStyle(ball, null).width
                return parseInt(r)
          }
          return this.ballR
      }  
  },
  methods:{
      //全国字的点击事件
      allClick() {
          this.$emit('nationClick')
      },
      //地球的宽高
      updateEath() {
            let content = document.querySelector(".content")
            let width = window.getComputedStyle(content, null).width
            //z-index 
            let zIndex = this.com / 7.55
            //   console.log(zIndex)
            content.setAttribute('style',`height:${width};`)
            let s = parseInt(width) /883
            //地球的z-index
            let earth = document.querySelector(".earth")
            earth.setAttribute('style',`z-index: ${parseInt(zIndex)};`)
            //   console.log(s)
            //设置全国文字的z-index region-name
            let region = document.querySelector(".region-name")
            region.setAttribute('style',`z-index: ${parseInt(zIndex+1)};transform: scale(${s});`)

        
      },
      //获取ratio标签下的span值
      updateRatioColor() {
        //   let value = document.querySelector(".star")
        //   console.log(value)
      },
      //根据球体的大小变化来改变，字体的大小
      updateByBall() {
          this.hengTest.forEach((item, index) => {
              let con = document.querySelector(".star" + index).children[0]
            //   let con1 = con.children[0]
            //   let value = con.children[1]
              let ratio = con.children[2]
              let width = parseInt(window.getComputedStyle(con, null).width)
            //   let height = window.getComputedStyle(con, null).height
              //166px为标准，进行缩放与放大
              console.log(width)
              let s = width / 136
              console.log(s)
              con.setAttribute('style',`transform: scale(${s});`);

              //获取ratio标签下的span值
              let span = ratio.children[0]
              let spanText = span.innerHTML.slice(0,1)
            //   console.log(span)
              if (spanText == '+') {
                  span.setAttribute('style',`color: red;`);
              }else if (spanText == '-') {
                  span.setAttribute('style',`color: green;`);
              }
              
          })
      },
      //设置球体的半径
      ballRadius() {
          if (this.R == 0) {
                let ball = document.querySelector(".star")
                this.R = window.getComputedStyle(ball, null).width
          }
          
      },
      //获取小球运动路径的所有坐标与缩放度，
      ani_track(w, h, init0) {
         let a = parseInt(w) * this.widthRatio 
         let b = parseInt(h) * this.heightRatio
         //初始角度    (当初始位负数或则朝过360的时候，待考虑该情况) 当前情况初始度数只能位
         //大于0，小于360
         let init = init0 + 90
         if (init >= 360) init = init -360
         //初始弧度
         let initDeg = init * (Math.PI / 180)
         //每次行动的角度
         let every = (360 / this.com) * (Math.PI / 180)
         //记录运动时的0%的位置
         //  let sportInit
         //球体的放大与缩小的倍数
         let maxScale = this.maxScale
         let minScale = this.minScale
         //计算球体每帧的缩小或放大量
         let scale = (maxScale - minScale) / this.com * 2
         //计算初始位置的放大量
         //  let scale0 = (init - 0) / every * scale + (maxScale + minScale) / 2
         let scale0 = this.countInitScale(init, every, maxScale, minScale, scale)
         //  let scale0 = (maxScale + minScale) / 2
        //设置z-index 最前面的球体为999
        let zIndex = parseInt(this.com / 2) + 1
         for (let i = 0; i < this.com; i ++) {
            //x坐标与y坐标，保留2位小数
            let x = Math.floor(a * Math.cos(initDeg) * 100) /100
            let y = Math.floor(b * Math.sin(initDeg) * 100) /100
            initDeg += every
            // if (i == 0) sportInit = `100%{transform: translate(${x}px, ${y}px) scale(${scale0});}`
            //设置球体放大与缩小
            // scale0 = Math.floor((scale0 + this.addScale(x) * scale) * 100) / 100
            scale0 = scale0 + this.addScale(x, y) * scale
            //保存所有的坐标与缩放倍数
            let ballXYS = {
                x: x,
                y: y,
                s: scale0,
                z: zIndex
            }
            if (x >= 0) {
                zIndex ++
            }else {
                zIndex --
            }
            this.ballSportXY.push(ballXYS)
            //保存逆时针的坐标集
            this.ballSportXYReverse.unshift({ballXYS})
            // console.log(bfb, scale0)
         } 
        //  console.log('all', this.ballSportXY)  
        //  console.log('all', this.ballSportXYReverse)  
      },
      //判断球体在坐标中的位置，来进行放大与缩小
      addScale(x0, y0) {
          //直接取整数值
          let x = parseInt(x0)
          let y = parseInt(y0)
          if (x > 0 && y != 0) {//球体位于第一象限或则第四象限的时候球体放大
            return 1
          }else if (x < 0 && y != 0) {//球体位于第二象限或则第三象限的时候球体缩小
            return -1
          }else {
              return 0
          }
      },
      //通过传递的角度计算，小球的初始位置时的放大量；
      //init:初始角度，every:每帧转动的弧度，最大的放大倍数，最小的放大倍数
        countInitScale(init, every, maxScale, minScale, scale) {
            // (init - 0) * (Math.PI / 180) / every * scale + (maxScale + minScale) / 2
            let initScale = 0
            //取余，保证在90以内
            let init0 = init % 90
            let scaleValue = (init0 - 0) * (Math.PI / 180) / every * scale 
            // console.log('scaleValue', scaleValue)
            if (init >=0 && init < 90) {//第四象限
                initScale = scaleValue + (maxScale + minScale) / 2
            }else if (init >= 90 && init < 180) {//第三象限
                initScale = maxScale - scaleValue 
            }else if (init >= 180 && init < 270) {//第二象限
                initScale = (maxScale + minScale) / 2 - scaleValue
            }else {//第一象限
                initScale = minScale + scaleValue
            }
            return initScale
        },
        //让小球运动起来
        letBallSport(time, flag) {
            clearInterval(this.parseTime1)
            clearTimeout(this.parseTime3)
            this.parseTime1 = setInterval(() => {
                // let anyTimeXYNow = this.anyTimeXY
                this.anyTimeXY = []
                this.hengTest.forEach((item, index) => {
                    let ind = (index * this.ballEveryStep +this.step) % this.com
                    let x = this.ballSportXY[ind].x
                    let y = this.ballSportXY[ind].y
                    let s = this.ballSportXY[ind].s
                    let z = this.ballSportXY[ind].z
                    this.anyTimeXY.push({
                        x: x,
                        y: y,
                        s: s
                    })
                    let div = document.querySelector('.star' + index)
                    // console.log(x, y)
                    div.setAttribute('style',`transform: translate(${x}px, ${y}px) scale(${s});width:${this.ballRR}px;height:${this.ballRR}px;
                    z-index: ${z}`); 
                })
                // this.step ++
                if (flag == 0) {//小球顺时针转
                    this.step ++
                    if (this.step == this.com) this.step = 0
                }else {//小球逆时针转
                    if (this.step == 0) this.step = this.com
                    this.step --
                }
                // console.log(this.step)
            }, time)

        },
        //小球的初始位置
        ballInitPos() {
            this.hengTest.forEach((item, index) => {
                let ind = index * this.ballEveryStep
                let x = this.ballSportXY[ind].x
                let y = this.ballSportXY[ind].y
                let s = this.ballSportXY[ind].s
                let z = this.ballSportXY[ind].z
                // console.log(ind)
                let div = document.querySelector('.star' + index)
                div.setAttribute('style',`transform: translate(${x}px, ${y}px) scale(${s});width:${this.ballRR}px;height:${this.ballRR}px;
                z-index: ${z}`);
            })
        },
        //点击事件，被点击的小球回到屏幕的最前面
        clickEvent(ind) {
            //当小球被点击的时候,判断小球所处的象限，然后判断旋转方向
            // console.log(ind)
            let div = document.querySelector('.star' + ind)
            //   console.log(div.style)
            let mat = window.getComputedStyle(div, null).getPropertyValue('transform')
            //打印得结果位：matrix(0.861937, 0, 0, 0.861937, -222.527, -165.499)
            //截取第5个元素 -222.527
            let x = mat.slice(6).split(',')[4]
            // let y = mat.slice(6,-1).split(',')[5]
            if (x < 0) {
                this.clickOneParse(ind)
                this.letBallSport(this.clickTimeMS, 1)
            }else {
                this.clickOneParse(ind)
                this.letBallSport(this.clickTimeMS, 0)
            }
            this.clickBallLight(ind)
            //返回给父组件的小球点击事件
            this.$emit('ballClick',ind)
        },
        //需要让被点击的小球再最前面停止，其他的小球不停止;
        clickOneParse(ind) {
            this.ballFlag = []
            this.hengTest.forEach((item, index) => {
                if (index != ind)
                this.ballFlag.push(index)
            })
        },
        //该定时器得作用是实时记录小球得x轴和y轴得变化；
        realTimeSport() {
            this.parseTime2 = setInterval(() => {
                //   clearTimeout(this.parseTime2)
                this.hengTest.forEach((item, index) => {
                    let div = document.querySelector('.star' + index)
                    //   console.log(div.style)
                    let mat = window.getComputedStyle(div, null).getPropertyValue('transform')
                    //打印得结果位：matrix(0.861937, 0, 0, 0.861937, -222.527, -165.499)
                    //截取第5个元素 -222.527
                    let x = mat.slice(6).split(',')[4]
                    let y = mat.slice(6,-1).split(',')[5]
                    if (x == this.ballSportXY[0].x && this.ballFlag.indexOf(index) == -1 && y > 0) {
                        clearInterval(this.parseTime1)
                        this.ballFlag = []
                        this.ballFlag.push(index)
                        // console.log(111111)clickBall
                        //当小球位于最前面的时候，有发亮的样式
                        let arr = document.getElementsByClassName("star"+index)
                        arr[0].classList.add("clickBall")
                        this.parseTime3 = setTimeout(() => {
                            // this.realTimeSport()
                            arr[0].classList.remove("clickBall")
                            this.letBallSport(this.everyStepTime, 0)
                        }, this.ballParseTimeMS)
                    }
                      
                },0)
            })
        },
        //当小球被点击的时候，发亮
        clickBallLight(ind) {
            this.hengTest.forEach((item, index) => {
                let arr = document.getElementsByClassName("star"+index)
                if (index == ind) {
                    //当小球被点击的时候，小球发亮
                    arr[0].classList.add("clickBall")
                }else {
                    arr[0].classList.remove("clickBall")
                }
            })
        }


  }
}
</script>

